@media (max-width: 1024px) {

    .what_card {
        background-color: transparent;
        color: white;
    }

    .what {
        background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url("../image/image_what.png");
        background-attachment: fixed;
    }

    .what_zar {
        border: 2px solid white;
    }

    .indicators {
        display: none;
    } 
}

@media (max-width: 540px) {
    .mini-header ul {
        gap: 10px;
    }

    .mini-header ul li a {
        font-size: 16px;
    }

    header h1 {
        font-size: 50px;
    }

    .slide-content h1 {
        font-size: 30px;
        margin: 0 0 10px;
    }
      
    .slide-content p {
        font-size: 20px;
        margin: 0;
        width: 50vw;
    }

    .typewriter h1 {
        font-size: 24px;
    }

    .footer {
        font-size: 26px;
    }

    .what_card p {
        width: 50vw;
    }
}

@media (max-width: 912px) {
    .mini-header ul {
        gap: 55px;
    }

    .mini-header ul li a {
        font-size: 24px;
    }

    header h1 {
        font-size: 80px;
    }

    .slide-content h1 {
        font-size: 50px;
        margin: 0 0 10px;
    }
      
    .slide-content p {
        font-size: 30px;
        margin: 0;
        width: 50vw;
    }

    .typewriter h1 {
        font-size: 40px;
    }

    .footer {
        font-size: 26px;
    }

    .what_card p {
        width: 50vw;
    }
}

@media (max-width: 414px) {
    .mini-header ul {
        gap: 10px;
    }

    .mini-header ul li a {
        font-size: 16px;
    }

    header h1 {
        font-size: 50px;
        text-align: center;
    }
    .slide-content p{
        display: none;
    }

    .typewriter h1 {
        height: 70vh;
    }

    .overlayDemon {
        height: 70vh;
    }

    .demonList iframe {
        width: 100%;
    }

    .download-header-text p {
        display: none;
    }
    .download-game h1 {
        display: none;
    }
    .download-game a {
        width: 50%;
    }
    .academy-thanks {
        height: 170vh;
    }
}

@media (max-width: 280px) {
    .mini-header ul li a {
        font-size: 14px;
    }
    header p {
        text-align: center;
        font-size: 14px;
    }
    .what_card h1 {
        font-size: 40px;
    }
    .overlayDemon {
        height: 80vh;
    }
}