header {
    height: 100vh;
    background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url('../image/image.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 80px;
}

header h1 {
    font-size: 110px;
}

header p {
    font-size: 16px;
}

.slider {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    transition: all 1000ms;
  }
  
  .slide {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    transition: opacity 0.5s ease-in-out;
    transition: all 1000ms;
  }
  
  .slide.active {
    opacity: 1;
  }
  
  .slide-content {
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 20px;
    color: #fff;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.5);
  }
  
  .slide-content h1 {
    font-size: 60px;
    margin: 0 0 10px;
  }
  
  .slide-content p {
    font-size: 30px;
    margin: 0;
    width: 100vw;
  }
  
  .controls {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 0 20px;
    box-sizing: border-box;
  }
  
  .controls .left_btn, .controls .right_btn {
    background-color: transparent;
    border: none;
    color: #fff;
    font-size: 30px;
    cursor: pointer;
    transition: transform 0.2s ease-in-out;
  }
  
  .controls .left_btn:hover, .controls .right_btn:hover {
    transform: scale(1.2);
  }

  .indicators {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 80vh;
  }
  
  .indicators button.noactive {
    background: transparent;
    border: 2px solid #fff;
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
    margin: 0 0.5rem;
    cursor: pointer;
    transform: scale(1);
  }
  
  .indicators button.active {
    background-color: white;
    transform: scale(1);
    border: 1px solid #fff;
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
    margin: 0 0.5rem;
    cursor: pointer;
  }

  .indicators button.noactive:hover {
    background-color: #fff;
    transition: all 300ms;
  }

  .typewriter {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100%;
    background: transparent;
    overflow: hidden;
}

.typewriter h1 {
    color: #fff;
    font-size: 3rem;
    font-weight: 500;
    text-align: center;
    overflow: hidden;
    white-space: wrap;
    margin: 0 auto;
    letter-spacing: .25em;
    animation: typing 3.5s steps(40, end), blink-caret .75s step-end infinite;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 50vh;
    width: 100%;
    background-color: #fff;
    color: black;
    z-index: 100;
    position: absolute;
}

#video {
    width: 100%;
    position: relative;
    z-index: -1;
}

.what {
    height: 100vh;
    background: url("../image/image_what.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.what_card {
    background-color: white;
    width: 40%;
    height: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    gap: 55px;
}

.what_card h1 {
    font-size: 48px;
}

.what_zar {
    display: block;
    border: 2px solid black;
    padding: 0 25px;
}

.what_card p {
    width: 25vw;
}