.download-header-text {
    color: #fff;
    font-size: 3rem;
    font-weight: 500;
    text-align: center;
    overflow: hidden;
    margin: 0 auto;
    animation: typing 3.5s steps(40, end), blink-caret .75s step-end infinite;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 50vh;
    width: 100%;
    background-color: #fff;
    color: black;
    z-index: 50;
    margin-top: auto;
}

.download-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100%;
    background: url(../image/download.png);
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

.download-game {
    display: flex;
    flex-direction: row;
    position: relative;
    align-items: center;
    justify-content: center;
    z-index: 100;
    margin: auto 0;
}

.download-game h1 {
    color: #fff;
    font-size: 1.5rem;
    font-weight: 500;
    text-align: center;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 15vh;
    width: 50%;
    background-color: #fff;
    color: black;
    z-index: 100;
}

.download-game img {
    display: block;
    width: 100%;
}

.download-game a {
    width: 20%;
}