.overlayDemon {
    color: #fff;
    font-size: 3rem;
    font-weight: 500;
    text-align: center;
    overflow: hidden;
    white-space: wrap;
    margin: 0 auto;
    animation: typing 3.5s steps(40, end), blink-caret .75s step-end infinite;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 50vh;
    width: 100%;
    background-color: #fff;
    color: black;
    z-index: 90;
    position: absolute;
}

.header-demon {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100%;
    background: transparent;
    overflow: hidden;
}

.demonList {
    margin-top: 100px;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.demonList iframe {
    margin-top: 100px;
    width: 70%;
    height: 100%;
}