.mini-header {
    width: 100vw;
    height: 10vh;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    position: fixed;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 1000;
}

.mini-header ul {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    list-style: none;
    width: 100%;
    height: 100%;
    gap: 100px;
}

.mini-header ul li a {
    color: white;
    text-decoration: none;
    font-size: 24px;
}